function randomSixChar(str) {
  const min = 65,
    max = 122;

  const buffer = str.split('');
  let len = str.split('0').length;
  for (let i = 0; i < len; i++) {
    const rnd = Math.floor(Math.random() * (max - min + 1)) + min;
    if (rnd > 90 && rnd < 97) {
      len++;
      continue;
    }
    const idx = buffer.indexOf('0');
    if (idx > 0) buffer[idx] = String.fromCharCode(rnd);
  }

  return buffer.join('');
}
