import React, { useState, memo } from 'react';
import { toast } from 'react-hot-toast';
function Items({ idx, setItemList, itemList }) {
  const initialInvoiceItems = {
    name: '',
    qty: '',
    price: '',
    total: '',
    tax: '',
    id: idx,
  };
  const [invoiceItems, setInvoiceItems] = useState(initialInvoiceItems);

  const handleInputValue = (e) => {
    e.preventDefault();
    const { name, value } = e.target;
    setInvoiceItems((prev) => {
      const tmp = { ...prev, [name]: value };

      // prettier-ignore
      return {
        ...tmp,
        total: parseFloat(tmp.price * tmp.qty) + parseFloat((tmp.price*tmp.qty) * (tmp.tax / 100)),
      };
    });
  };

  return (
    <div className='my-4 flex flex-col gap-1 rounded-lg px-2 bg-slate-100'>
      <div className='my-4 flex justify-between'>
        <label htmlFor='age' className='mb-2 font-semibold'>
          Item {idx}
        </label>
        <textarea
          id='item'
          value={invoiceItems.name}
          name='name'
          className='w-1/2 rounded-lg border border-slate-200 px-2 py-1 hover:border-blue-500 focus:outline-none focus:ring focus:ring-blue-500/40 active:ring active:ring-blue-500/40'
          onChange={handleInputValue}
        ></textarea>
      </div>
      <div className='mb-4 flex justify-between'>
        <label htmlFor='age' className='mb-2 font-semibold'>
          Qty
        </label>
        <input
          type='number'
          id='qty'
          name='qty'
          value={invoiceItems.qty}
          className='w-1/2 rounded-lg border border-slate-200 px-2 py-1 hover:border-blue-500 focus:outline-none focus:ring focus:ring-blue-500/40 active:ring active:ring-blue-500/40'
          onChange={handleInputValue}
        />
      </div>
      <div className='mb-4 flex justify-between'>
        <label htmlFor='age' className='mb-2 font-semibold'>
          Unit Price
        </label>
        <input
          type='number'
          name='price'
          value={invoiceItems.price}
          className='w-1/2 rounded-lg border border-slate-200 px-2 py-1 hover:border-blue-500 focus:outline-none focus:ring focus:ring-blue-500/40 active:ring active:ring-blue-500/40'
          onChange={handleInputValue}
        />
      </div>
      <div className='mb-4 flex justify-between'>
        <label htmlFor='age' className='mb-2 font-semibold'>
          tax
        </label>
        <input
          type='number'
          name='tax'
          value={invoiceItems.tax}
          className='w-1/2 rounded-lg border border-slate-200 px-2 py-1 hover:border-blue-500 focus:outline-none focus:ring focus:ring-blue-500/40 active:ring active:ring-blue-500/40'
          onChange={handleInputValue}
        />
      </div>
      <div className='mb-4 flex justify-between'>
        <label htmlFor='age' className='mb-2 font-semibold'>
          Total
        </label>

        <input
          type='number'
          name='total'
          id='total'
          value={invoiceItems.total}
          onChange={() => ''}
          className='w-1/2 rounded-lg border border-slate-200 px-2 py-1 hover:border-blue-500 focus:outline-none focus:ring focus:ring-blue-500/40 active:ring active:ring-blue-500/40'
        />
      </div>
      <div className='flex my-2 justify-end'>
        <button
          className='ml-2 py-1 px-2 bg-green-600 rounded text-white'
          onClick={(e) => {
            e.preventDefault();

            if (itemList.filter((item) => item.id === idx).length > 0) {
              toast.error('Item Already exist');
            } else if (
              invoiceItems.name !== '' &&
              invoiceItems.qty !== 0 &&
              invoiceItems.price !== 0
            ) {
              setItemList((prev) => [...prev, invoiceItems]);
              toast.success(`Item ${idx} Added`);
            } else {
              toast.error(`Item ${idx} field required`);
            }
          }}
        >
          add
        </button>
        <button
          className='ml-2 py-1 px-2 bg-red-600 rounded text-white'
          onClick={(e) => {
            e.preventDefault();
            if (itemList.length > 0) {
              const list = itemList.filter((i) => i.id !== idx);
              setItemList(list);
              toast.success(`Item ${idx} removed`);
            } else toast.error('Item list is empty');
          }}
        >
          remove
        </button>
      </div>
    </div>
  );
}
export default memo(Items);
